1,100..900&display=swap");
.auth {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
}

.auth label,
.auth p,
.auth span,
.auth a,
.auth h1,
.auth h2,
.auth h3,
.auth h4,
.auth h5,
.auth h6 {
  color: #042a42;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

.auth a {
  color: #042a42;
  font-size: 13px;
}

.auth label {
  font-size: 13px;
  margin-bottom: 0.25rem;
}

.auth .form-group {
  margin-bottom: 1rem;
  position: relative;
}

.auth .form-group label {
  font-size: 11px;
  background: #f6f6f6;
  display: block;
  position: relative;
  transform: translateY(10px);
  z-index: 1;
  border: 1px solid #ebebeb;
  border-bottom: none;
  border-radius: 8px 8px 0 0;
  padding-left: 12px;
}

.auth .form-group .form-control {
  border-radius: 8px;
  border-color: #ebebeb;
  background: #f6f6f6;
}

.auth p {
  font-size: 15px;
}

.auth .btn-eye {
  position: absolute;
  right: 0;
  top: 3px;
  background-color: transparent;
  border: none;
  color: rgba(0, 0, 0, 0.25);
}

.auth .btn-eye:hover,
.auth .btn-eye:focus {
  background-color: transparent;
  color: rgba(0, 0, 0, 0.75);
}

.auth.auth__form-left .auth__image {
  order: 2;
}

.auth.auth__form-left .auth__form {
  order: 1;
}

.auth.auth__form-left .auth__logo-form {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.auth.auth__form-left .auth__title {
  text-align: center;
  margin-bottom: 3rem;
}

.auth__logo-form {
  max-width: 300px;
}

.auth__image {
  flex: 1;
  background-color: #fff;
  height: 100%;
  position: relative;
  background-position: center;
  background-size: auto;
  background-repeat: no-repeat;
}

.auth__form {
  height: 100%;
  width: 42%;
  background-color: #fff;
  padding: 4rem;
  display: flex;
  align-items: center;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.5);
}

.auth__form form {
  width: 100%;
}

.auth__title {
  font-size: 2rem;
  color: #042a42;
  line-height: 1.3;
  font-weight: 400;
  margin-bottom: 2rem;
}

.btn-signin {
  background-color: #042a42;
  font-family: "Roboto", sans-serif;
  color: #fff;
  padding: 1rem;
  margin-top: 25px;
  border-radius: 8px;
}

.btn-signin:hover {
  opacity: 0.9;
}

.auth__logo {
  position: absolute;
  max-width: 90px;
}

.auth__logo.top-left {
  left: 30px;
  top: 30px;
}

.auth__logo.top-right {
  right: 30px;
  top: 30px;
}

.auth__logo.bottom-left {
  left: 30px;
  bottom: 30px;
}

.auth__logo.bottom-right {
  right: 30px;
  bottom: 30px;
}

.auth__bg {
  object-fit: cover;
  height: 100%;
}

.auth__social {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 80px;
}

.auth__social a {
  width: 34px;
  height: 34px;
  margin: 0 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #042a42;
  border-radius: 50%;
  color: #fff;
  transition: all 0.15s linear;
}

.auth__social a:hover {
  text-decoration: none;
  background-color: black;
}

@media (max-width: 1024px) {
  .auth__image {
    background-size: contain;
  }

  .auth__form {
    width: 45%;
  }
}

@media (max-width: 800px) {
  .auth__image {
    display: none;
  }

  .auth__form {
    padding-left: 2rem;
    padding-right: 2rem;
    min-height: 500px;
    overflow-y: auto;
    max-width: 100%;
    width: 100%;
  }

  .auth__logo-form {
    display: block;
  }
}




